/* Snowflake animation */
@keyframes snowflake {
  from {
    transform: translateY(-10px) rotate(0deg);
  }
  to {
    transform: translateY(100vh) rotate(360deg);
  }
}

::-webkit-scrollbar {
  display: none; /* For Chrome, Safari, and Opera */
}

.msctls_scrollbar {
  display: none; /* For Internet Explorer */
}

.scrollable-content {
  -ms-overflow-style: none;  /* For Microsoft Edge */
}
