/* Chat.css */
.chat-container {
    @apply min-h-screen flex flex-col items-center justify-center bg-gray-50;
  }
  
  .chat-header {
    @apply flex items-center bg-blue-500 text-white p-4 rounded-lg shadow-lg w-full max-w-4xl;
  }
  
  .chat-icon {
    @apply flex items-center justify-center bg-blue-700 text-white p-3 rounded-full mr-4;
  }
  
  .chat-messages {
    @apply flex-grow w-full max-w-4xl p-4 overflow-y-auto bg-white shadow-lg rounded-lg mt-4;
  }
  
  .chat-message {
    @apply p-4 mb-4 rounded-lg text-white;
    &.user {
      @apply bg-blue-500 self-end;
    }
    &.ai {
      @apply bg-gray-700 self-start;
    }
  }
  
  .chat-input {
    @apply flex items-center bg-gray-200 p-4 rounded-lg shadow-lg w-full max-w-4xl mt-4;
    input {
      @apply flex-grow p-3 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500;
    }
    button {
      @apply ml-4 p-3 rounded-lg bg-blue-500 text-white hover:bg-blue-600;
    }
  }
  